export const validation = (value, type) => {
  const letters = /[A-z-]/
  const numbers = /[0-9]/
  const email = /^[-\w.]+@([A-z0-9][-A-z0-9]+\.)+[A-z]{2,4}$/
  const phone = /^((\+7|7|8)+([0-9]){10})$/
  if (value) {
    switch (type) {
      case 'name':
        return value.length > 2 && value.match(letters) ? '' : '     3-     '
      case 'phone':
        return value.match(phone)? '' : '  '
      case 'email':
        return value.match(email) ? '' : ' e-mail'
      case 'password':
        return value.length > 6 && value.match(numbers) && value.match(letters) ? '' : '    6       '
      case 'description':
        return value.length > 40 ? '' : '     40 '
      default:
        return ''
    }
  } return ''
}